<template>
<section>
 <div class="row">
                <div class="col-md-12 padding-t_8">
                    <div class="theme-primary partition-full">
                        <span class="p-name text-white">{{$t('Add')}}</span>
                    </div>
                </div>
            </div>
           
<div class="border p-3">
    <Form ref="AddChain">
        <div class="row">       
            <div class="col-lg-12">
            <dynamic-form lang="en" :buttons="buttons" :schema="FormSchema" v-on:OnSubmit="onSubmit"></dynamic-form>
            </div>   
        </div>
        <div class="row">
            <div class="listing mt-3 px-3">
                <div class="table-responsive">
                    <table class="table table-bordered dt-responsive nowrap mt-0 dataTable no-footer dtr-inline" id="tbContainer">
                        <thead class="thead-bg">
                            <tr>
                                <th><span>{{$t('Level')}}:<sup class="text-danger">*</sup></span></th>
                                <th><span>{{$t('Group')}}:<sup class="text-danger">*</sup></span></th>
                                <th class="text-center single-action"><a href="javascript:;" @click="AddRow" class="round-icon-small btn-success-light d-block float-none m-auto" style="line-height: 33px;" id="btnAdd" title="Add"><i class="fa fa-plus"></i></a></th>
                            </tr>
                        </thead>
                        <tbody>
                            <tr class="mainleveldiv" v-for="(item,index) in Mainleveldiv" :key="index">
                                <td>
                                    <Field :name="'Level'" v-slot="{errors}" :rules="{ 'required': true }" >
                                        <select :class="{'form-control':true,'is-invalid':errors[0]}" :name="item.name" v-model="item.value" @change="AlreadyExist($event,index,item,'level')" class="form-control level">
                                            <option value>Select Level</option>
                                            <option v-for="pitem in Level" :key="pitem"  :value="pitem.value">{{pitem.name}}</option>
                                        </select>
                                         <span v-if="errors[0]" :name="'Level'+index" class="invalid-feedback d-block">{{ errors[0] }}</span>
                                    </Field> 
                                </td>
                                <td>
                                    <Field :name="'Group'" v-slot="{errors}" :rules="{ 'required': true }" >
                                        <select :class="{'form-control':true,'is-invalid':errors[0]}" v-model="item.Group" @change="AlreadyExist($event,index,item,'group')">
                                            <option value>Select Ticket Group</option>
                                            <option v-for="gitem in GroupData" :value="gitem.value" :key="gitem">{{gitem.name}}</option>
                                        </select>
                                        <span v-if="errors[0]" :name="'Group'" class="invalid-feedback d-block">{{ errors[0] }}</span>
                                    </Field> 
                                </td>
                               <td class='text-center single-action'  v-if="index!=0"><a href='javascript:;' id='btnRemove' @click="DeleteUser(index)" class='round-icon-small btn-danger-light d-block float-none m-auto' title='Remove'><i class='fa fa-trash'></i></a></td>
                            </tr>
                        </tbody>
                    </table>
                </div>
            </div>
        </div>
        <div class="col-12 mt-3">
            <div class="row flex-row-reverse">
                <div class="col-lg-6 text-right">
                    <a href="javascript:;" id="aChainTicketSave" class="btn btn-success" @click="SaveData" title="Save"><i class="fa fa-floppy-o pr-2"></i><span>{{$t('Save')}}</span></a>&nbsp;
                    <RouterLink to="/Ticketing/Chain"  title="Cancel" id="gobacktolistscreen" class="btn btn-danger"><i class="fa fa-times pr-2"></i><span>{{$t('Cancel')}}</span></RouterLink>
                </div>
                <div class="col-lg-6">
                    <div class="col-lg-6"><div class="py-2"><span class="text-danger">{{$t('MandatoryString')}}</span></div></div>
                </div>
            </div>
        </div>
            
    </Form> 
 </div>
</section>
</template>
<script>
import DataServices from "@/services/DataService";
export default {
    created:function(){
        var vm=this;
        vm.GetTicketGroupsForDropdownList();
        if(vm.$route.params.id!=0){
            vm.GetChainData(vm.$route.params.id);
        }
    },
     data() {
        return {
            Mainleveldiv:[{value:'',Group:''}],
            GroupData:[],
            FormSchema: [
                {
                    layoutType: "two",
                    Data: [                                
                        {
                            astype: "TextField",
                            label: 'Ticket Service Board Name',
                            name: "TicketChainName",
                            value: "",
                            placeholder: "",
                            validationRules: { "required": true, max: 250 },                            
                        },
                        {
                            astype: "TextAreaField",
                            label: this.$t('description'),
                            name: "Description",
                            value: "",
                            placeholder: this.$t('EnterDescription'),
                            validationRules: { "required": true, max: 1000 },
                        }
                    ]
                }
            ],
            Level:[
                {
                    name:'Level 1',
                    value:1
                },
                {
                    name:'Level 2',
                    value:2
                },
                {
                    name:'Level 3',
                    value:3
                },
                {
                    name:'Level 4',
                    value:4
                },
                {
                    name:'Level 5',
                    value:5
                },
                {
                    name:'Level 6',
                    value:6
                },
                {
                    name:'Level 7',
                    value:7
                },
                {
                    name:'Level 8',
                    value:8
                },
                {
                    name:'Level 9',
                    value:9
                },
                {
                    name:'Level 10',
                    value:10
                }
            ],
           buttons: [
                {
                    type: "submit",
                    class: "btn btn-warning",
                    text: "<i class='fa fa-save pr-2'></i>Save Of Draft",                    
                    isVisible:false
                }
            ],
        }
    },
    methods:{
        AddRow:function(e){
            var vm=this;
            vm.Mainleveldiv.push({
                value:'',
                Group:''
            })
        },
        GetTicketGroupsForDropdownList:function(){
            var vm=this;
            DataServices.GetTicketGroupsForDropdownList().then(function (response) {
                response.data.forEach((item) => {
                    vm.GroupData.push({
                        name:item.ticket_group_name,
                        value:item.ticket_group_id
                    })
                })
            })
        },
        AlreadyExist:function(e,i,item,lbl){
            var vm=this;
            if (vm.Mainleveldiv.some(item => item.value === e.target.value)) 
            {
                vm.Mainleveldiv[i].value='';
                vm.ShowAlert('Already Selected',"warning");
            }
            else if (vm.Mainleveldiv.some(item => item.Group === e.target.value)) 
            {
                vm.Mainleveldiv[i].Group='';
                vm.ShowAlert('Already Selected',"warning");
            }
            else if(lbl=='level')
            {
                vm.Mainleveldiv[i].value=e.target.value;
            }
            else if(lbl=='group')
            {
                 vm.Mainleveldiv[i].Group=e.target.value;
            }
        },
        GetChainData:function(id){
            var vm=this;
            var params=`ticketChainId=${id}`
            DataServices.GetChainData(params).then(response => {
                vm.FormSchema[0].Data[0].value=response.data.DATA[0].ticket_chain_name;
                vm.FormSchema[0].Data[1].value=response.data.DATA[0].description;
                vm.Mainleveldiv=[];
                 response.data.DATA.forEach((item, index) => {
                    vm.Mainleveldiv.push({
                        value:item.ticket_group_level,
                        Group:item.group_id
                    })
                    
                 })
            })
        },
        DeleteUser:function(id){
            var vm=this;
            vm.Mainleveldiv.splice(id, 1);
        },
        SaveData:function(){
            var vm=this;
            vm.$refs.AddChain.validate().then(result=>{
                if(result){
                var params=`serviceBoardId=${vm.$route.params.id}&serviceBoardName=${vm.FormSchema[0].Data[0].value}&description=${vm.FormSchema[0].Data[1].value}&LevelIdsOrGroupIds=${JSON.stringify(vm.Mainleveldiv)}`
                    DataServices.AddTicketServiceBoard(params).then(response => {
                        if(response.data==true){
                            vm.ShowAlert('Ticket ServiceBoard has been succesfully added',"success");
                            vm.$router.push({ path: '/Ticketing/Chain' });
                        }
                        else{
                            vm.ShowAlert(vm.$t('GroupHasAdded'),"success");
                        }
                    })
                }
            }) 
        }
    }
}

</script>